      position: absolute
      margin-top: -27px
      margin-left: 20px
      padding: 0 2px
      font-size: 15px
      font-weight: 500
      font-style: italic
      color: #999

  .hover--blue:hover
    background: $colors.blue.base !important

  .hover--green:hover
    background: $colors.green.base !important

  .hover--orange:hover
    background: $colors.orange.base !important

  .hover--red:hover
    background: $colors.red.base !important

  .hover--teal:hover
    background: $colors.teal.base !important

  .display-box
    background: #424242
    border-radius: 2px
    opacity: 0.75
    -webkit-transition: 0.3s ease
    transition: 0.3s ease

    &:hover
      opacity: 1

</style>



